@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;

    &_light {
        .name,
        .subtitle-block {
            @include color("gray", "0");
        }

        .ellipse-icon {
            @include color("opacities", "white-4");
        }
    }
}

.image,
.img {
    width: 231px;
    max-width: 231px;
    height: 180px;

    @include respond-to("laptop") {
        width: 100%;
        max-width: 376px;
        height: 280px;
    }
}

.image {
    object-fit: cover;
}

.img {
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.ellipse-icon {
    display: flex;
    width: 20px;
    margin-left: -2px;
    color: rgba(66, 78, 88, 0.1);
}

.name {
    font-size: 17px;
    line-height: 24px;

    margin-top: 16px;
    margin-bottom: 0;

    @include respond-to("laptop") {
        font-size: 28px;
        line-height: 36px;
    }
}

.content {
    font-weight: normal;
    font-size: 17px;
    line-height: 24px;

    margin-bottom: 16px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;

        margin-bottom: 8px;
    }
}

.subtitle-block {
    font-weight: normal;
    font-size: 13px;
    line-height: 20px;

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    margin-bottom: 8px;

    @include respond-to("laptop") {
        font-size: 17px;
        line-height: 28px;
    }
}
