@import "style/mixins.module.scss";

.root {
    &_light {
        .title {
            @include color("opacities", "white-7");
        }
    }
}

.content {
    display: flex;
    width: fit-content;
    margin-left: 24px;
}

.title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 40px;
    }

    i {
        @include color("primary", "red");

        font-style: normal;
    }
}

.review {
    margin-right: 8px;

    width: 100%;

    &:last-child {
        margin-right: 24px;
    }
}

.desktop {
    display: none;
    margin-bottom: 160px;
}

.mobile {
    display: block;

    margin: 0 -24px 80px -24px;
}

.mobile_with_border {
    position: relative;
    margin: 0 -24px 15px -24px;
    padding-bottom: 24px;

    &::after {
        content: "";
        display: block;
        position: absolute;
        width: calc(100% - 48px);
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
        height: 1px;
        background-color: rgba(66, 78, 88, 0.1);
    }
}

@media (min-width: 1024px) {
    .content {
        margin-left: 0;
    }

    .review {
        margin-right: 40px;

        width: 33.33%;

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            padding-right: 0;
            margin-right: 0;
        }
    }

    .title {
        margin-bottom: 40px;
    }

    .desktop {
        display: block;
    }

    .desktop_with_border {
        padding-bottom: 24px;
        margin-bottom: 24px;
        position: relative;

        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            bottom: 0;
            left: 50%;
            transform: translate(-50%);
            height: 1px;
            background-color: rgba(66, 78, 88, 0.1);
        }
    }

    .mobile {
        display: none;
    }
}
