@import "style/mixins.module.scss";

.section {
    display: flex;

    @include respond-to("mobile") {
        flex-direction: column-reverse;
    }

    &__picture {
        display: block;

        @include respond-to("mobile") {
            margin-bottom: 65px;
        }

        @include respond-to("laptop") {
            width: calc(50% + 40px);
            margin-right: -40px;
            margin-left: 0;
        }

        @include respond-to("container-default") {
            width: calc(50% + 8vw);
            margin-right: -8vw;
        }

        @include respond-to("laptop-XL") {
            width: calc(50% + 196px);
            margin-right: -196px;
        }
    }

    &__image {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        position: relative;

        @include respond-to("laptop") {
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 50%;
            padding-right: 40px;
        }
    }
}
