@import "style/mixins.module.scss";

.root {
    padding-top: 24px;
    padding-bottom: 24px;

    @include respond-to("laptop") {
        padding-bottom: 110px;
    }
}

.main {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 160px;
    }

    &__title {
        margin-bottom: 16px;
    }

    &__description {
        margin: 0;
        font-size: 17px;
        line-height: 24px;

        i {
            @include color("primary", "red");

            font-style: normal;
        }

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__badge {
        display: block;

        @include respond-to("mobile") {
            position: absolute;
            top: -104px;
            left: -4px;
            width: 125px;
            height: 64px;
        }

        @include respond-to("laptop") {
            width: 207px;
            height: 108px;
            margin-bottom: 40px;
        }
    }

    &__logo {
        display: block;
        height: 28px;
        width: 140px;
        margin-bottom: 40px;

        svg {
            width: 100%;
            height: 100%;
        }

        @include respond-to("laptop") {
            height: 32px;
            width: 160px;
            margin-bottom: 20px;
        }
    }
}

.booking,
.results {
    margin-bottom: 65px;
}

.booking {
    @include respond-to("laptop") {
        width: 800px;
        margin: 0 auto 160px;
        text-align: center;
    }

    &__title {
        display: none;

        @include respond-to("laptop") {
            display: inline-block;
            margin-bottom: 40px;
        }
    }

    &__form {
        @include respond-to("laptop") {
            width: 376px;
            margin: 0 auto;
            text-align: left;
        }
    }
}

.terms {
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    i {
        @include color("primary", "blue");

        font-style: normal;
    }

    @include respond-to("laptop") {
        text-align: center;
    }

    @include respond-to("laptop") {
        text-align: center;
    }
}
